export default {
  options: {
    virtualHost: true,
  },
  props: {
    errMessage: {
      type: String,
      default: '很抱歉因系统原因，本场比赛无法启动直播',
    },
    isError: {
      type: Boolean,
      default: false,
    },
    isLoading: {
      type: Boolean,
      default: false,
    },
    isShowPlayIcon: {
      type: Boolean,
      default: false,
    },
    reloadInterval: {
      type: Number,
      default: 5,
    },
    retryTimeout: {
      type: Number,
      default: 300,
      // default: 5,
    },
    showFeedbackIcon: {
      type: Boolean,
      default: false,
    },
    showGoOtherMatch: {
      type: Boolean,
      default: true,
    },
  },
  emits: [
    'clickFeedbackIcon',
    'goOtherMatch',
    'reloadPlayer',
    'startPlay',
  ],
  data() {
    return {
      isTryFailed: false,
      timeout: this.retryTimeout,
      intervalT: null,
    };
  },
  watch: {
    isError: {
      handler(newVal) {
        if (newVal) {
          if (this.intervalT) {
            return;
          }
          if (this.retryTimeout <= 0) {
            this.stopTimer();
            return;
          }

          // 5s reload一次
          const reloadInterval = this.reloadInterval || 5;

          this.intervalT = setInterval(() => {
            this.timeout -= 1;

            if (this.timeout % reloadInterval === 0) {
              this.$emit('reloadPlayer');
            }

            if (this.timeout <= 0) {
              this.stopTimer();
            }
          }, 1000);
        } else {
          if (this.intervalT) {
            this.timeout = this.retryTimeout;
            this.isTryFailed = false;

            clearInterval(this.intervalT);
            this.intervalT = null;
          }
        }
      },
      immediate: true,
    },
  },
  methods: {
    stopTimer() {
      clearInterval(this.intervalT);
      this.intervalT = null;
      this.isTryFailed = true;
    },
  },
};
